import React, { useEffect } from "react";
import "./Pages.css";
import { useHistory, Link } from "react-router-dom"
import Logo from "../images/SapporoLogo.png"
import Facebook from '../images/sap_facebook.jpg'
import Instagram from '../images/sap_ig.jpg'
/**
 * @author Decory Herbert
 * @function Home
 **/

const Home = (props) => {
  let history = useHistory()

  useEffect(()=>{
if(localStorage.getItem('revisit') == 'true'){
  fetch("https://bottlecapdev.pythonanywhere.com/visits", {
    method: "POST",
    mode: "cors",
    headers: {
      "Content-Type": "application/x-www-form-urlencoded",
      "Access-Control-Allow-Origin": "*",
    },
    body: JSON.stringify({
    'visited': 'true',
    })
  }).then((resp) => resp.json())
  .then((respr)=>{
    console.log(JSON.stringify(respr))
  })
}
if(localStorage.getItem('revisit') == null || localStorage.getItem('revisit') == undefined){
  localStorage.setItem('revisit', 'true')
  fetch("https://bottlecapdev.pythonanywhere.com/visits", {
    method: "POST",
    mode: "cors",
    headers: {
      "Content-Type": "application/x-www-form-urlencoded",
      "Access-Control-Allow-Origin": "*",
    },
    body: JSON.stringify({
    'visited': 'false',
    })
  }).then((resp) => resp.json())
  .then((respr)=>{
    console.log(JSON.stringify(respr))
  })
}
  },[])

  const load_game = () => {
    window.location.replace('https://sapporobeer.com')
  }
  const milkcom = () => {
    window.location.replace('https://milk.com/')
  }
  return (
      <div className="home_container">
        <img className='logo' src={Logo} alt="Sapporo Logo" />
        {/* <div className="win_container">
          <h1><span className="win">WIN</span> A HOME <br /> STUDIO</h1>
        </div> */}
        <div className="of-age_container" style={{color: 'black', fontSize: '1.5em'}}>
          <p>THIS CONTEST IS NOW CLOSED. <br /> THANK YOU FOR YOUR PARTICIPATION.</p>
        </div>
        <div className="button_container">
          <button onClick={load_game} style={{backgroundColor: 'black', color: 'white', border: '2px solid #ff9933', fontSize: '1.6em', width: 'auto', height: 'auto', padding: '10px', paddingLeft:'40px', paddingRight:'40px'}}>GO TO SAPPOROBEER.COM</button>
          <div className='socials_wrapper'> 
          <a href='https://www.facebook.com/SapporoBeerAsia'><img className='socials' src={Facebook} alt="Sapporo Facebook Logo" /></a>
          <a href='https://www.instagram.com/sapporocanada/'><img className='socials' src={Instagram} alt="Sapporo Instagram Logo" /></a>
          </div>
        </div>
      </div>
  );
};

export default Home;


